<style lang="css" scoped>
    #Map {
        padding: 30px;
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .left {
        padding: 15px;
        display: grid;
        justify-items: center;
        align-items: center;
    }

    .left img {
        width: 100%;
    }

/* ---- Map ----  */
    svg path {
        fill: var(--main-red-color);
        stroke: var(--main-white-color);
        stroke-width: 1px;
        transition: fill 0.3s;
    }

    svg path:hover {
        fill: var(--main-green-color);
        stroke: var(--main-white-color);
        stroke-width: 1px;
        transition: fill 0.3s;
    }

    .right {
        padding: 15px;
    }

    .right .list {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .right .text p {
        text-indent: 2em;
        text-align: justify;
    }

    table {
        width: 100%;
        border: 1px solid var(--main-grey-color);
    }

    td {
        width: 50%;
        height: 30px;
        border: 1px solid var(--main-grey-color);
    }

    thead {
        background-color: var(--main-grey-color);
        color: var(--main-white-color);
    }


/* --- Search Box --- */
    .search-box {
        margin-bottom: 20px;
        padding: 10px;
        height: 30px;
        border-radius: 40px;
        background: var(--main-dark-blue-color);
    }

    .search-box:hover > .search-txt {
        width: 240px;
        padding: 0 6px;
    }

    .search-box:hover > .search-btn {
        background: white;
    }

    .search-txt:focus {
        width: 240px;
    }

    .search-btn {
        color: var(--home-red-color);
        width: 40px;
        height: 30px;
        border-radius: 50%;
        background: var(--main-dark-blue-color);
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.6s;
    }

    .search-txt {
        border: none;
        background: none;
